"use client";

import {useEffect, useState} from "react";

export default function Form() {
    let [name, setNames] = useState();
    let [lastNames, setLastNames] = useState();
    let [email, setEmail] = useState();
    let [nickName, setNickName] = useState();
    let [password, setNickName] = useState();



    const handleSubmit = (event) => {
        event.preventDefault();
        console.log(`Data user: \n 
            names: ${name}\n
            lastNames: ${lastNames}\n
            email: ${email}\n
            nickName: ${nickName}\n`
        );

    };

    const onChange = (event, setValue) => setValue(event.target.value);
    



    return (
        <div>
            <h1>Hola, bienvenido a mi formulario</h1>
            <p>Por favor ingresa tus tus datos para continuar</p>
            <form onSubmit={handleSubmit}>
                <label>Nombres:</label>
                <input
                name="names"
                type="text"
                onChange={(event) => onChange(event, setNames)}
                />

                <label>Apellidos:</label>
                <input
                    name="lastNames"
                    type="text"
                    onChange={(event) => onChange(event, setLastNames)}
                />

                <label>Email:</label>
                    <input
                    name="email"
                    type="email"
                    onChange={(event) => onChange(event, setEmail)}
                />

                <label>Password:</label>
                    <input
                    name="password"
                    onChange={(event) => onChange(event, setPassword)}
                />

                <label>Username (Tu NickName):</label>
                    <input
                    name="nickName"
                    type="text"
                    onChange={(event) => onChange(event, setNickName)}
                />
                
                <input type="submit" value="Enviar"/>
            </form>
        </div>
    )
}